@media only screen and (max-width: 767px) {
  .filter {
    margin-top: 124px;
    margin-bottom: 0;
  }
}

.filter__main {
  position: relative;
  padding-top: 60px;
}

@media only screen and (max-width: 767px) {
  .filter__main {
    padding-top: 0;
  }
}

.filter__layout {
  text-align: center;
  margin: 0 -80px;
  transition: opacity 1s, visibility 1s;
}

@media only screen and (max-width: 767px) {
  .filter__layout {
    height: auto !important;
    padding-left: 0;
    margin-left: -15px;
    margin-right: -15px;
  }
}

.filter__label {
  font-size: 17px;
  color: #abadb0;
  text-align: left;
}

.filter__label--dark {
  color: #646466;
}

.filter__buttons {
  position: absolute;
  top: 0;
  left: 0;
  display: flex;
  flex-flow: column;
  z-index: 5;
}

.filter__buttons--main {
  user-select: none;
}

@media only screen and (max-width: 767px) {
  .filter__buttons--main {
    position: static;
    margin-bottom: 52px;
  }
}

.filter__buttons--filter {
  margin-top: 168px;
  left: 36px;
}

@media only screen and (max-width: 767px) {
  .filter__buttons--filter {
    left: 15px;
    margin-top: 124px;
  }
}

.filter__btn {
  font-size: 20px;
  color: #abadb0;
  text-align: left;
  line-height: 1.4;
}

.filter__btn.is-active {
  color: #646466;
}

.filter__btn--selected {
  color: #646466;
}

.filter__btn[data-filter="*"] {
  text-decoration: underline;
}

.filter__item {
  display: inline-block;
  padding: 0 80px 60px;
  position: static !important;
  top: auto !important;
  left: auto !important;
}

@media only screen and (max-width: 767px) {
  .filter__item {
    padding: 0 15px 10px;
    max-width: 100%;
  }
}

.filter__image {
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  padding-bottom: 247px;
  position: relative;
  z-index: 3;
  transition: 2.5s transform;
}

.filter__image:hover {
  transform: translateY(42px);
  transition: 0.7s transform;
}

@media only screen and (max-width: 767px) {
  .filter__image:hover {
    transform: translatey(0);
  }
}

.filter__image--small {
  width: 164px;
}

@media only screen and (max-width: 767px) {
  .filter__image--small {
    padding-bottom: 120%;
  }
}

.filter__image--middle {
  width: 350px;
}

@media only screen and (max-width: 767px) {
  .filter__image--middle {
    max-width: 100%;
    padding-bottom: 72%;
  }
}

.filter__image--big {
  width: 439px;
}

@media only screen and (max-width: 767px) {
  .filter__image--big {
    max-width: 100%;
    padding-bottom: 58%;
  }
}

.filter__text {
  position: relative;
  top: -247px;
  left: 50%;
  transform: translateX(-50%);
  z-index: 1;
}

@media only screen and (max-width: 767px) {
  .filter__text {
    display: none;
  }
}

.filter__next {
  text-align: center;
  position: relative;
  margin-top: 54px;
  margin-bottom: 64px;
}

.filter__next .icon {
  fill: #ff4d00;
  font-size: 0.75em;
}

.filter__filter {
  opacity: 0;
  visibility: hidden;
  transition: opacity 1s, visibility 1s;
}

.filter__filter.is-active {
  opacity: 1;
  visibility: visible;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  z-index: 10000;
  background-color: #262626;
  overflow: hidden;
  transition: opacity 1s, visibility 1s;
}

#slider {
  background-color: #262626;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
}

.slider-title {
  font-size: 20px;
  color: #ff4d00;
  text-decoration: underline;
  font-weight: 400;
  margin-top: 140px;
}

@media only screen and (max-width: 767px) {
  .slider-title {
    display: none;
  }
}

.slider {
  width: 140%;
  z-index: 4;
  opacity: 0;
  margin-left: -65px;
  visibility: hidden;
  transition: opacity 2s, visibility 2s;
}

@media only screen and (max-width: 767px) {
  .slider {
    width: calc(100% + 15px);
    margin: 0;
  }
}

.slider-container {
  padding-top: 140px;
  text-align: center;
  font-size: 0;
  height: 400px;
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
}

.slider-container:before {
  content: "";
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 100%;
}

@media only screen and (max-width: 767px) {
  .slider-container {
    padding-top: 0;
  }
}

.slider-wrapper {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  max-width: 1430px;
}

@media only screen and (max-width: 1429px) {
  .slider-wrapper {
    max-width: 100% !important;
  }
}

.slider:before {
  position: absolute;
  top: 0;
  bottom: 0;
  right: calc(100% - 65px);
  background-color: #262626;
  z-index: 100;
  content: '';
  width: 20%;
}

@media only screen and (max-width: 767px) {
  .slider:before {
    display: none;
  }
}

.slide {
  transition: transform 1.4s;
  font-size: 20px;
}

@media only screen and (max-width: 767px) {
  .slide {
    padding: 0 8px 40px;
  }
}

.active.center .slide {
  transform: translateY(42px);
  transition: transform 1.4s;
}

@media only screen and (max-width: 767px) {
  .active.center .slide {
    transform: translateY(0);
  }
}

.slide__img {
  padding-bottom: 410px;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: 50% 50%;
  z-index: 3;
  transition: transform 0.8s;
}

@media only screen and (max-width: 767px) {
  .slide__img {
    padding-bottom: 270px;
    width: 250px;
  }
}

.slide__text {
  display: block;
  position: relative;
  top: -410px;
  left: 0;
  z-index: -1;
  color: #abadb0;
  transition: transform 1.4s;
}

.slide__text:hover .arrow {
  visibility: visible;
  opacity: 1;
}

.active.center .slide__text {
  transform: translateY(-42px);
  transition: transform 1.4s;
}

@media only screen and (max-width: 767px) {
  .active.center .slide__text {
    transform: translateY(0);
  }
}

@media only screen and (max-width: 767px) {
  .slide__text {
    top: 16px;
    z-index: 100;
  }
}

.owl-item {
  min-width: 200px;
  max-width: 613px;
}

@media only screen and (max-width: 767px) {
  .owl-item {
    max-width: 250px;
  }
}

[data-image*="parent"] {
  overflow: hidden;
  transform: translate3d(-100%, 0, 0);
}

[data-image*="parent"].is-active {
  transform: translate3d(0, 0, 0);
  transition-delay: 0.5s;
}

[data-image*="parent"].is-active [data-image*="child"] {
  transition-delay: 0.5s;
  transform: translate3d(0, 0, 0);
}

[data-image*="parent"].is-active [data-image*="child"] img {
  max-width: 100%;
}

[data-image*="child"] {
  transform: translate3d(100%, 0, 0);
}

[data-image] {
  transition: transform 1s cubic-bezier(0.8, 0, 0.2, 1);
}

.text-row {
  position: relative;
  transform: translateY(80px);
  opacity: 0;
}

.js-title .text-row, .js-title-secondary .text-row {
  position: relative;
  transform: translateY(0);
  opacity: 1;
}

.js-title span, .js-title-secondary span {
  position: relative;
  transform: translateX(30px);
  opacity: 0;
  display: inline-block;
}

.sr-up {
  transform: translateY(50px);
}
