.fullpage-wrapper {
    position: absolute !important;
    top: 0;
    left: 0;
    width: 100%;
    height: 100% !important;
    overflow: hidden;
    z-index: 100;
    transform: none !important;
    min-height: 100% !important;
    @include sm {
        overflow: auto;
        margin-top: $main-margin-top-sm;
        height: auto !important;
        position: relative !important; } }

.section {
    position: absolute !important;
    top: 0;
    left: 0;
    width: 100%;
    height: 100% !important;
    opacity: 0;
    visibility: hidden;
    // z-index: 1
    transition: opacity 0.5s ease-out, visibility 0.5s ease-out;

    overflow: auto;
    &.active {
        opacity: 1;
        visibility: visible;
 }        // z-index: 1
    .fp-tableCell {
        padding-top: 90px;
        padding-bottom: 60px;
        @include sm {
            padding: 0;
            display: block;
            height: auto !important; } }
    @include sm {
        opacity: 1;
        visibility: visible;
        position: relative !important;
        height: auto !important; } }

.image {
    display: inline-block;
    font-size: 0; }

@include sm {
    #fullpage {
        .section {
            margin-bottom: 70px;
            .feature-list,
            .contacts-list,
            .title,
            .fp-text,
            .about_results {
                display: none; } }
        .section {
            // .image
            //     display: block
            .project_link {
                display: block;
                margin-bottom: 18px; } }
        .wrapper {
            min-height: inherit; }
        [class$="vh"] {
            margin-bottom: 0;
            margin-top: 0; }
        .fp-section.fp-table, .fp-slide.fp-table {
            display: block; } }
    .all-projects-sm {
        margin-top: 24px;
        margin-bottom: 30px; } }


.letter {
    display: inline-block;
    position: relative;
    min-width: 5px;
    vertical-align: top; }
.text-row {
    display: block; }

.wrapper {
    width: 100%;
    font-size: 20px;
    position: relative;
    text-align: left;
    min-height: 570px;
    @include css-lock(16,20,767,1400);
    img {
        display: block;
        // +rh(700)
 } }        //     width: calc(75% + (100 - 75) * ((100vh - 600px) / (700 - 600)))

.titleLarge {
    font-size: 30px;
    line-height: 1.5;
    max-width: 220px;
    @include sm {
        font-size: 20px;
        font-weight: 400;
        margin-bottom: 40px; } }

.project_link {
    font-size: 20px;
    line-height: 1.5;
    font-weight: 400; }

.project_link {
    display: block;
    max-width: 200px; }

.project_link .text-row:last-child,
.adress .text-row:last-child {
    position: relative;
    &:after {
        content: '→';
        position: absolute;
        top: 50%;
        left: calc(100% + 4px);
        transform: translateY(-50%);
        visibility: hidden;
        opacity: 0;
        transition: 0.8s opacity, 0.8s visibility; }
    &:hover {
        &:after {
            visibility: visible;
            opacity: 1;
            transition: 0.8s opacity, 0.8s visibility; } } }

.motto {
    @include sm {
        margin-bottom: 30px;
        display: block !important; } }

.title {
    font-size: 18px;
    font-weight: 400; }

.link {
    color: $light-grey; }

.fp-list-text {
    max-width: 180px; }

.adress {
    max-width: 280px; }

